import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';
import {Routes, RouterModule} from '@angular/router';
import {GroupsComponent} from './group-components/groups.component';
import {GroupComponent} from './group-components/group.component';
import {ThingsComponent} from './group-components/things.component';
import {ThingsTableComponent} from './group-components/things-table.component';
import {LatestModalComponent} from './group-components/latest-modal.component';
import {ThingComponent} from './group-components/thing.component';
import {LatestTableComponent} from './group-components/latest-table.component';
import {NgxHumanizeDurationModule} from 'ngx-humanize-duration';
import {TsGraphComponent} from './group-components/ts-graph/ts-graph.component';
import {LandingComponent} from './group-components/landing.component';
import {Daterangepicker} from 'ng2-daterangepicker';
import {NgChartsModule} from 'ng2-charts';
import Annotation from 'chartjs-plugin-annotation';
import {Decimation} from 'chart.js';
import {AttributesTableComponent} from "./group-components/attributes-table.component";

const routes: Routes = [
  {
    path: 'groupsstart',
    data: {
      title: 'Groups',
      urls: [{title: 'Groups', url: '/groupsstart'}, {title: 'Groups'}],
    },
    component: LandingComponent,
  },
  {
    path: 'groups',
    data: {
      title: '',
      urls: [{title: 'Groups', url: '/groups'}, {title: 'Groups'}],
    },
    component: GroupsComponent,
  },
  {
    path: 'groups/:groupId',
    data: {
      title: '',
      urls: [{title: 'Group', url: '/groups/:groupId'}, {title: 'Group'}],
    },
    component: GroupComponent,
  },
  {
    path: 'things',
    data: {
      title: '',
      urls: [{title: 'Things', url: '/things'}, {title: 'Things'}],
    },
    component: ThingsComponent,
  },
  {
    path: 'groups/:groupId/thing/:thingId',
    data: {
      title: '',
      urls: [{title: 'Thing', url: '/groups/:groupId/thing/:thingId'}, {title: 'Thing'}],
    },
    component: ThingComponent,
  }
];

@NgModule({
  imports: [
    NgChartsModule.forRoot({
      defaults: {},
      plugins: [Annotation, Decimation]
    }),
    Daterangepicker,
    FormsModule,
    CommonModule,
    RouterModule.forChild(routes),
    NgxHumanizeDurationModule,
  ],
  declarations: [
    LandingComponent,
    GroupComponent,
    GroupsComponent,
    ThingComponent,
    ThingsComponent,
    ThingsTableComponent,
    LatestModalComponent,
    LatestTableComponent,
    AttributesTableComponent,
    TsGraphComponent
  ],
})
export class GroupModule {

}
