import {Component, AfterViewInit, OnInit} from '@angular/core';
import {API} from '@aws-amplify/api';
import {ActivatedRoute} from '@angular/router';
import {ApiConfig} from '../../shared/api.config';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  templateUrl: './group.component.html'
})
export class GroupComponent implements AfterViewInit, OnInit {
  subtitle: string;

  groupId: string | null;

  group: any = null;
  things: any = null;
  subscriptions: any = null;
  subscribeOfflineEmail = '';
  subscribeOfflineSubmit = 'Subscribe';

  constructor(private activatedRoute: ActivatedRoute,
              private modalService: NgbModal) {
    this.subtitle = 'This is some text within a card block.';
    this.groupId = null;
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      this.groupId = params.get('groupId');
    });
    API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId, ApiConfig.params)
      .then(response => {
        this.group = response.data;
      })
      .catch(error => {
        console.log(error.response);
      });
    API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThings, ApiConfig.params)
      .then(response => {
        this.things = response.data.things;
      })
      .catch(error => {
        console.log(error.response);
      });
    API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathSubscribeOffline, ApiConfig.params)
      .then(response => {
        this.subscriptions = response.data.subscriptions;
      })
      .catch(error => {
        console.log(error.response);
      });
  }

  ngAfterViewInit() {
  }

  openModal(modal: string) {
    this.modalService.open(modal, {size: 'lg'});
  }

  subscribeOfflineNotifications() {
    console.log(this.subscribeOfflineEmail);
    this.subscribeOfflineSubmit = '<i class="fas fa-spinner fa-spin"></i>';
    API.post(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathSubscribeOffline,
      {body: {email: this.subscribeOfflineEmail}})
      .then(response => {
        console.log(response);
        this.subscribeOfflineSubmit = 'Subscribed!';
        this.subscribeOfflineEmail = '';
        setTimeout(() => {
          this.subscribeOfflineSubmit = 'Subscribe';
        }, 1000);
      })
      .catch(error => {
        console.log(error);
      });
  }
}
