import {Component, AfterViewInit, OnInit} from '@angular/core';
import {API} from '@aws-amplify/api';
import {ApiConfig} from '../../shared/api.config';
import {ActivatedRoute} from '@angular/router';

@Component({
  templateUrl: './thing.component.html'
})
export class ThingComponent implements AfterViewInit, OnInit {
  subtitle: string;

  groupId: string | null;
  thingId: string | null;

  thing: any = null;
  sensorValues: any = {};
  sensorUnits: any = {};
  nowTime = new Date().getTime();

  constructor(private activatedRoute: ActivatedRoute) {
    this.subtitle = 'This is some text within a card block.';
    this.groupId = null;
    this.thingId = null;
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      this.groupId = params.get('groupId');
      this.thingId = params.get('thingId');
    });

    API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThing + this.thingId, ApiConfig.params)
      .then(response => {
        this.thing = response.data;
        if (this.thing.hasOwnProperty('lastSensorValues')) {
          this.sensorValues = this.thing['lastSensorValues'];
        }
        if (this.thing.hasOwnProperty('sensorUnits')) {
          this.sensorUnits = this.thing['sensorUnits'];
        }
        console.log(this.thing);
      })
      .catch(error => {
        console.log(error.response);
      });
  }

  ngAfterViewInit() {
  }
}
