import {Component, AfterViewInit, OnInit, Input} from '@angular/core';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-latest-modal',
  templateUrl: './latest-modal.component.html',
  styleUrls: []
})
export class LatestModalComponent implements AfterViewInit, OnInit {
  objectKeys = Object.keys;
  @Input()
  thing: any = {};
  sensorValues: any = {};
  sensorUnits: any = {};

  nowTime = new Date().getTime();

  constructor(private modalService: NgbModal) {
  }

  ngOnInit(): void {
    if (this.thing.hasOwnProperty('lastSensorValues')) {
      this.sensorValues = this.thing['lastSensorValues'];
    }
    if (this.thing.hasOwnProperty('sensorUnits')) {
      this.sensorUnits = this.thing['sensorUnits'];
    }
  }

  ngAfterViewInit() {
  }

  openModal(modal: string) {
    this.modalService.open(modal, {size: 'lg'});
  }
}
