import {Component, AfterViewInit, OnInit, Input} from "@angular/core";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {API} from "@aws-amplify/api";
import {ApiConfig} from "../../shared/api.config";
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'app-attributes-table',
  templateUrl: './attributes-table.component.html',
  styleUrls: []
})
export class AttributesTableComponent implements AfterViewInit, OnInit {
  objectKeys = Object.keys;
  @Input() groupId: any;
  @Input() thingId: any | null;

  group: any = null;
  thing: any = null;
  attributes: any = {};
  attributeKey: string = '';
  attributeValue: string = '';
  attributeAddBtn = 'Add';
  attributeUpdateBtn: string = 'Update';
  attributeDeleteBtn: string = 'Delete';
  thingPath: string = '';
  groupPath: string = '';

  constructor(private activatedRoute: ActivatedRoute,
              private modalService: NgbModal) {
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      this.groupId = params.get('groupId');
      this.thingId = params.get('thingId');
    });
    if (!this.thingId) {
      API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId, ApiConfig.params)
        .then(response => {
          this.group = response.data;
          if (this.group.hasOwnProperty('attributes')) {
            this.attributes = this.group['attributes'];
          }
        })
        .catch(error => {
          console.log(error.response);
        });
    } else {
      API.get(ApiConfig.apiName, ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThing + this.thingId, ApiConfig.params)
        .then(response => {
          this.thing = response.data;
          if (this.thing.hasOwnProperty('attributes')) {
            this.attributes = this.thing['attributes'];
          }
          console.log(this.thing);
        })
        .catch(error => {
          console.log(error.response);
        });
    }
  }

  openAddAttributeModal(modal: string) {
    this.modalService.open(modal, {size: 'lg'});
  }

  openEditAttributeModal(modal: string, rowKey: string, rowValue: string) {
    this.attributeKey = rowKey;
    this.attributeValue = rowValue;
    this.thingPath = ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThing + this.thingId + ApiConfig.paths.pathAttribute;
    this.groupPath = ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathAttribute;
    this.modalService.open(modal, {size: 'lg'});
  }

  ngAfterViewInit() {
  }

  addAttribute() {
    let params = {body: {[this.attributeKey]: this.attributeValue}};
    console.log(params);
    this.attributeAddBtn = '<i class="fas fa-spinner fa-spin"></i>';

    let thingPath = ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThing + this.thingId + ApiConfig.paths.pathAttribute;
    let groupPath = ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathAttribute;

    API.post(ApiConfig.apiName, this.thingId == null ? groupPath : thingPath, params)
      .then(response => {
        console.log(response);
        this.ngOnInit();
        this.attributeAddBtn = 'Added!';
        setTimeout(() => {
          this.attributeAddBtn = 'Add';
        }, 1000);
      })
      .catch(error => {
        console.log(error);
      });
  }

  deleteAttribute() {
    console.log('deleting attribute key: ' + this.attributeKey);
    let params = {body: {[this.attributeKey]: ''}};
    this.attributeDeleteBtn = '<i class="fas fa-spinner fa-spin"></i>';
    API.post(ApiConfig.apiName, this.thingId == null ? this.groupPath : this.thingPath, params)
      .then(response => {
        console.log(response);
        this.ngOnInit();
        this.modalService.dismissAll();
        this.attributeDeleteBtn = 'Delete';
      })
      .catch(error => {
        console.log(error);
      });
  }

  editAttribute() {
    let params = {body: {[this.attributeKey]: this.attributeValue}};
    console.log(params);
    this.attributeUpdateBtn = '<i class="fas fa-spinner fa-spin"></i>';
    API.post(ApiConfig.apiName, this.thingId == null ? this.groupPath : this.thingPath, params)
      .then(response => {
        console.log(response);
        this.ngOnInit();
        this.attributeUpdateBtn = 'Updated!';
        setTimeout(() => {
          this.attributeUpdateBtn = 'Update';
        }, 1000);
      })
      .catch(error => {
        console.log(error);
      });
  }
}
