import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {API} from '@aws-amplify/api';
import {ApiConfig} from '../../../shared/api.config';
import {ActivatedRoute} from '@angular/router';
import moment from 'moment';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import html2canvas from 'html2canvas';
import {ChartConfiguration, ChartType, DecimationAlgorithm} from 'chart.js';
import {BaseChartDirective} from 'ng2-charts';
import 'chartjs-adapter-date-fns';

@Component({
  selector: 'app-ts-graph',
  templateUrl: './ts-graph.component.html',
  styleUrls: ['./ts-graph.component.css']
})
export class TsGraphComponent implements OnInit {
  objectKeys = Object.keys;

  groupId: string | null;
  thingId: string | null;
  measureName: string | '';
  loading = false;
  dataError = false;

  @Input()
  sensorValues: any = {};
  @Input()
  sensorUnits: any = {};

  @ViewChild(BaseChartDirective) chart?: BaseChartDirective;

  measureNames: string[] = [];

  endTime = moment();
  startTime = moment().subtract(1, 'hours');

  chartStartFromZero = true;

  fullChartData = [];
  fullChartXLabels = [];

  public daterange: any = {};

  // see original project for full list of options
  // can also be setup using the config service to apply to multiple pickers
  public options: any = {
    locale: {format: 'YYYY-MM-DD HH:mm'},
    timePicker: true,
    timePicker24Hour: true,
    startDate: this.startTime,
    endDate: this.endTime,
    alwaysShowCalendars: false,
  };

  public lineChartData: ChartConfiguration['data'] = {
    datasets: [
      {
        data: [],
        label: 'sensorData',
        backgroundColor: 'rgba(148,159,177,0.2)',
        borderColor: 'rgba(148,159,177,1)',
        pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff',
        pointHoverBackgroundColor: '#fff',
        pointHoverBorderColor: 'rgba(148,159,177,0.8)',
      }
    ],
    // labels: []
  };

  public lineChartOptions: ChartConfiguration['options'] = {
    scales: {
      // We use this empty structure as a placeholder for dynamic theming.
      x: {
        type: 'time',
        time: {
          tooltipFormat: 'MMM dd, yyyy, HH:mm:ss',
          unit: 'hour',
          displayFormats: {
            'second': 'HH:mm:ss',
            'minute': 'dd/MM HH:mm',
            'hour': 'dd/MM HH:mm',
            'day': 'dd/MM',
            'week': 'dd MMM',
            'month': 'dd MMM',
            'quarter': 'dd MMM',
            'year': 'MMM YY',
          }
        }
      },
      y: {
        position: 'left',
        ticks: {},
        min: 0,
        grid: {
          color: 'rgba(0, 0, 0, 0.1)'
        }
      }
    },
    parsing: false,
    elements: {line: {tension: 0.5}},
    responsive: true,
    maintainAspectRatio: true,
    plugins: {
      legend: {display: false},
      tooltip: {
        mode: 'nearest',
        intersect: false,
      },
      // decimation: {
      //   enabled: true,
      //   algorithm: DecimationAlgorithm.lttb,
      //   samples: 100,
      //   threshold: 324
      // },
      // @ts-ignore
      annotation: {
        annotations: [],
      }
    }
  };

  public lineChartType: ChartType = 'line';

  constructor(private activatedRoute: ActivatedRoute,
              private modalService: NgbModal) {
    this.groupId = null;
    this.thingId = null;
    this.measureName = '';
  }

  selectMeasure(meas: any): void {
    this.measureName = meas;
    this.updateChart();
  }

  selectedDate(value: any, datepicker?: any) {
    // any object can be passed to the selected event and it will be passed back here
    datepicker.start = value.start;
    datepicker.end = value.end;

    this.startTime = value.start;
    this.endTime = value.end;

    // use passed valuable to update state
    this.daterange.start = value.start;
    this.daterange.end = value.end;
    this.daterange.label = value.label;

    this.updateChart();
  }

  updateChart(): void {
    this.loading = true;
    this.dataError = false;
    this.lineChartData.datasets[0]['label'] = this.measureName;

    const timeDiffHours = this.endTime.diff(this.startTime) / 60 / 60 / 1000;
    if (timeDiffHours < 4) {
      // @ts-ignore
      this.lineChartOptions.scales.x.time.unit = 'minute';
      // @ts-ignore
      this.lineChartOptions.scales.x.time.stepSize = 5;
    } else if (timeDiffHours < 24) {
      // @ts-ignore
      this.lineChartOptions.scales.x.time.unit = 'hour';
      // @ts-ignore
      this.lineChartOptions.scales.x.time.stepSize = 1;
    } else if (timeDiffHours < 72) {
      // @ts-ignore
      this.lineChartOptions.scales.x.time.unit = 'hour';
      // @ts-ignore
      this.lineChartOptions.scales.x.time.stepSize = 3;
    } else if (timeDiffHours < 7 * 24) {
      // @ts-ignore
      this.lineChartOptions.scales.x.time.unit = 'hour';
      // @ts-ignore
      this.lineChartOptions.scales.x.time.stepSize = 12;
    } else {
      // @ts-ignore
      this.lineChartOptions.scales.x.time.unit = 'hour';
      // @ts-ignore
      this.lineChartOptions.scales.x.time.stepSize = 24;
    }

    this.fullChartData = [];
    this.fullChartXLabels = [];

    const nextPageKey = '';
    this.getData(this.startTime.unix() * 1000, this.endTime.unix() * 1000, null);
  }

  getData(from: number, to: number, nextPageKey: any) {
    let url = ApiConfig.paths.pathGroup + this.groupId + ApiConfig.paths.pathThing + this.thingId + '/'
      + this.measureName + '/query/timerange';
    if (nextPageKey !== null) {
      url = url + '?pageKey=' + nextPageKey;
    }

    API.post(ApiConfig.apiName, url, {body: {from: from, to: to}})
      .then(response => {
        this.handleDataResponse(from, to, response);
      })
      .catch(error => {
        this.loading = false;
        this.dataError = true;
      });
  }

  handleDataResponse(from: number, to: number, response: any) {
    const d = response.data[this.measureName];
    const nextPageKey = response['_metadata'][0]['nextPageKey'];

    this.fullChartData = this.fullChartData.concat(d.map(function (e: any) {
      return {x: e.timestamp, y: e.value};
    }));
    this.fullChartXLabels = this.fullChartXLabels.concat(d.map(function (e: any) {
      return e.timestamp;
    }));

    console.log(this.fullChartData.length);

    if (nextPageKey === null) {
      this.lineChartData.datasets[0]['data'] = this.fullChartData;
      this.lineChartData.labels = this.fullChartXLabels;
      this.loading = false;
    } else {
      this.getData(from, to, nextPageKey);
    }
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe(params => {
      this.groupId = params.get('groupId');
      this.thingId = params.get('thingId');
    });
    this.measureNames = Object.keys(this.sensorValues);
    this.selectMeasure(this.measureNames[0]);
  }

  openModal(modal: string) {
    this.modalService.open(modal, {size: 'lg'});
  }

  updateChartSettings() {
    this.chartStartFromZero = (document.getElementById('chartStartFromZero') as HTMLInputElement).checked;
    // @ts-ignore
    this.lineChartOptions.scales.y.min = this.chartStartFromZero ? 0 : undefined;
  }

  downloadGraph() {
    html2canvas((document.getElementById('chart') as HTMLInputElement)).then(canvas => {
      const c = document.body.appendChild(canvas);
      let image = canvas.toDataURL('image/png');
      image = image.replace('image/png', 'image/octet-stream');
      const a = document.createElement('a');
      a.href = image;
      a.setAttribute('download', 'graph.png');
      a.click();
      a.remove();
      c.remove();
    });
  }
}
